
import React, { Component } from 'react';

import request from 'superagent'



class TvSeriesDetail extends Component{
	constructor(){
		super()
		this.state={
			tvSeries:[]
		}
	}

	componentDidMount(){
		var url = 'http://api.tvmaze.com/shows/' + this.props.match.params.id;
		request.get(url).end((error, responce) =>{
			if (!error && responce) {

				this.setState({ tvSeries: responce.body });
				console.log(this.state.tvSeries);
			}
			else{

				console.log(error)
			}	
		})
	}

	render(){
		let tvSeries = this.state.tvSeries;
		return(
			<div>
				<h2>Your details are here - </h2>
				<h1>{tvSeries.name}</h1>
				<img src={tvSeries.image && tvSeries.image.medium} />
				<h2> Summary  </h2>
				<div dangerouslySetInnerHTML={{__html: tvSeries.summary}} />
			</div>
				
			)
	}
}

export default TvSeriesDetail;


