import React, { Component } from 'react';
import request from 'superagent';
import { Link } from "react-router-dom";

class Search extends Component{
	constructor(){
		super();
		this.state = {
			searchResult:[]
	}
}

componentDidMount(){
	var url = 'http://api.tvmaze.com/search/shows?q=' + this.props.search_text;
	request.get(url).end((error, responce) => {
		if (!error && responce) {
			this.setState({ searchResult: responce.body });
			console.log(responce.body);
		}
		else{
			console.log(error)
		}
	}
	)
}
componentWillReceiveProps(nextProps){
	if (this.props.search_text !== nextProps.searchText) {
		var url = 'http://api.tvmaze.com/search/shows?q=' + nextProps.searchText;
		request.get(url).end((error, responce) =>{
			if (!error && responce) {

				this.setState({ searchResult: responce.body });
				console.log(this.state.searchResult);
			}
			else{

				console.log(error)
			}
		}
		)
	}
}

	render(){
		return(
			<div>
			<p> Your Results</p>
			{ this.state.searchResult.map((result, key) =>
				(<div>
					<Link to={'/detail/'+ (result.show && result.show.id)}>
					<img alt={result.show && result.show.name} src={result.show && result.show.image && result.show.image.medium} />
					<p>{result.show && result.show.name} </p>
					<br/>
				</Link>
				</div>)
				) }

			</div>
		)
	}
}

class TvSeries extends Component{
	constructor(){
		super();
		this.state = {
			searchText: '',
			searchStart: false,
		}
	}


handle = (e) =>  {
	this.setState({ searchText:e.target.value, searchStart:true })
}	

	render(){
		return(
			<div>
				<input type='search' onChange={this.handle} placeholder='Type Your TV Series' />
				{ this.state.searchStart ? (<Search searchText={this.state.searchText} />): (<h2> Search is not happening </h2>)}
			</div>	
		)
	}
}

export default TvSeries;


